// module import
import axios from "axios";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import Cookie from "js-cookie";

// hook import
import useInput from "../../hooks/useInput";

// config import
import { URL_LOGIN } from "../../config/config";

// css import
import styles from "./Member.module.css";
import LoginButton from "../button/LoginButton";

export default function Member(){
    // state  
    const [email, emailChange] = useInput(""); //   state
    const [pw, pwChange] = useInput(""); // pw  state
    const [error, setError] = useState(false); //    

    //   
    const url :string = URL_LOGIN;
    const loginErrStr :string = "   ."
    
    //  
    const minuteExpries :number = 30; // 
    const expiresTime :number = minuteExpries / 60 / 24; // 

    //   
    const movePage = useNavigate();

    // dataPost 
    let dataPost = async(email :string, pw :string) => {
        try{
            //  pw axios  url   jwt  
            const res = await axios.post(url, {
                email,
                pw,
            })

            //  
            if(res.status === 200 && res.data.length === 0){
                throw new Error("members/login axios.post   .");
            }

            Cookie.set("jwt", res.data, { expires: expiresTime, secure: true }); // jwt   

            return true;
        }
        //  
        catch(err){
            console.log(err); //   

            return false;
        }
    }

    //      
    let login = (e : React.MouseEvent<HTMLButtonElement>) => {
        e.preventDefault();

        dataPost(email, pw)
        .then((res) => {
            if(res === true){
                movePage("/"); //   
            }
            else{
                setError(true); //   
            }
        });
    }
    
    return (
        <div className={styles.body}>
            <form className={styles.form}>
                <input
                    className={styles.placeholder}
                    type="text" 
                    placeholder=""
                    value={email}
                    onChange={emailChange}
                />

                <input
                    className={styles.placeholder}
                    type="text" 
                    placeholder=""
                    value={pw}
                    onChange={pwChange}
                />
            </form>
            <section className={styles.errSection}>
                {error && (
                <div className={styles.errorText}>{loginErrStr}</div>
            )}
            </section>
            <LoginButton loginFunc = {login}/>
        </div>
    );
}