// module import
import { useNavigate } from "react-router";

// css import
import styles from "./AccessibleWindow.module.css";

export default function AccessibleWindow({setInfoFunc}){
    //   
    const movePage = useNavigate();

    //    ,    
    const handleLoginClick = (e) => {
        e.preventDefault();
        movePage("/login");
        setInfoFunc(false); //   ,   
    }

    return(
        <div className={styles.body}>
            <section className={styles.textSection}>
                <p className={styles.title}> .</p>
                <p className={styles.contents}>  .</p>
            </section>
            <nav className={styles.buttonSection}>
                <button className={styles.button} onClick={handleLoginClick}></button>
            </nav>
        </div>
    );
}